/* START GENERAL CSS */
@import 'fancybox.css';
*,
*::after,
*::before {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html, body {
  height: 100%;
  margin: 0;
}

body {
  font: 16px/1.3 "DM Sans", sans-serif;
  margin: 0;
  min-width: 320px;
  letter-spacing: 0.3px;
  background-color: #FFFFFF;
}

h1 {
  font-size: 28px;
  line-height: 1.14;
  font-weight: 700;
  margin: 0 0 15px;
}

h2, h3, h4, h5, h6 {
  font-size: 23px;
  line-height: 1.17;
  font-weight: 700;
  margin: 0 0 15px;
  letter-spacing: -1px;
}

/* General reset */
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* General reset jcf */
form, fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
}

input,
select,
textarea,
select,
button {
  vertical-align: middle;
  font: 16px/1.3 "DM Sans", sans-serif;
}

button,
input {
  *overflow: visible;
  line-height: normal;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  cursor: pointer;
  -webkit-appearance: button;
}

label,
select,
button,
input[type=button],
input[type=reset],
input[type=submit],
input[type=radio],
input[type=checkbox] {
  cursor: pointer;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

/* General reset html 5 */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

figure {
  margin: 0;
}

/* General reset */
img {
  border: 0;
  -ms-interpolation-mode: bicubic;
}

a {
  text-decoration: underline;
}
a:hover {
  text-decoration: none;
}
a:focus, a:hover {
  outline: none;
  outline: 0;
}

p {
  margin: 0 0 18px;
}

.hold-img img,
.hold-icon img {
  display: block;
  width: 100%;
  height: auto;
}

.default-input,
.default-textarea,
.default-checkbox {
  width: 100%;
  margin: 0 0 6px;
}

input[type=tel],
input[type=text],
input[type=email],
input[type=password] {
  width: 100%;
  height: 45px;
  padding: 12px;
  margin: 0 0 6px;
  outline: none;
  outline: 0;
  box-sizing: border-box;
  border: 1px solid #F0F4FF;
  border-radius: 8px;
  box-shadow: none;
  -webkit-appearance: none;
  transition: all 0.3s ease;
  position: relative;
  font-size: 16px;
  line-height: 16px;
  color: #080A13;
  font-weight: 400;
  text-align: left;
  outline: none;
  outline: 0;
  overflow: hidden;
}
input[type=tel]:focus,
input[type=text]:focus,
input[type=email]:focus,
input[type=password]:focus {
  border-color: #4F67F6;
}
input[type=tel]::placeholder,
input[type=text]::placeholder,
input[type=email]::placeholder,
input[type=password]::placeholder {
  color: #81838e;
  opacity: 1; /* Firefox */
}
input[type=tel]:-ms-input-placeholder,
input[type=text]:-ms-input-placeholder,
input[type=email]:-ms-input-placeholder,
input[type=password]:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #81838e;
}
input[type=tel]::-ms-input-placeholder,
input[type=text]::-ms-input-placeholder,
input[type=email]::-ms-input-placeholder,
input[type=password]::-ms-input-placeholder { /* Microsoft Edge */
  color: #81838e;
}
.error input[type=tel],
.error input[type=text],
.error input[type=email],
.error input[type=password] {
  border-color: #e50000;
}

form {
  position: relative;
  margin: 0;
  width: 100%;
}
form textarea {
  overflow: auto;
  vertical-align: top;
  border-radius: 8px;
  border: 1px solid #F0F4FF;
  width: 100%;
  padding: 12px;
  margin: 0 0 6px;
  outline: none;
  outline: 0;
  min-height: 172px;
  max-width: 100% !important;
  min-width: 100%;
  box-sizing: border-box;
  box-shadow: none;
  font-weight: 400;
  -webkit-appearance: none;
  transition: all 0.3s ease;
}
form textarea:focus {
  border-color: #4F67F6;
}
form textarea::placeholder {
  color: #81838e;
}
form textarea:-ms-input-placeholder {
  color: #81838e;
}
form textarea::-ms-input-placeholder {
  color: #81838e;
}
.error form textarea {
  border-color: #e50000;
}
form input[type=button],
form button {
  border: none;
  background: #080A13;
  text-align: center;
  width: 100%;
  font-weight: bold;
}
form label {
  width: 100%;
  display: block;
  position: relative;
}
form label:hover {
  cursor: default;
}
form input[readonly],
form button[readonly] {
  color: #080A13 !important;
  background: #080A13 !important;
  border: none !important;
}
form .btn {
  margin-top: 14px;
}

.error-message {
  display: none;
  font-size: 12px;
  color: #e50000;
  padding: 6px 0 6px 20px;
  position: relative;
  background: url(../images/icon-error.webp) no-repeat 0 6px;
  background-size: 15px 13px;
}
.error .error-message {
  display: block;
}

.default-checkbox {
  width: 100%;
  position: relative;
}
.default-checkbox input[type=checkbox] {
  display: none;
}
.default-checkbox__label {
  display: block;
  position: relative;
  padding: 2px 0 0 36px;
  cursor: pointer !important;
}
.default-checkbox__label:before {
  content: "";
  width: 24px;
  height: 24px;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 8px;
  border: 1px solid #F0F4FF;
  transition: border-color 0.1s ease;
}
.default-checkbox__label:after {
  content: "";
  position: absolute;
  top: 7px;
  left: 5px;
  width: 14px;
  height: 7px;
  border-left: 1px solid #1457FF;
  border-bottom: 1px solid #1457FF;
  transform: rotate(-45deg);
  opacity: 0;
  transition: opacity 0.1s ease;
}

.default-checkbox input[type=checkbox]:checked + .default-checkbox__label:before {
  border-color: #1457FF;
}

.default-checkbox input[type=checkbox]:checked + .default-checkbox__label:after {
  opacity: 1;
}

label {
  display: block;
  font-size: 14px;
  line-height: 1.43;
  color: #616472;
  padding: 0 5px;
  margin: 0 0 5px;
}

.btn {
  display: inline-block;
  font-size: 16px;
  line-height: 18px;
  padding: 15px 14px;
  border-radius: 12px;
  color: #fff;
  border: none;
  text-align: center;
  width: auto;
  font-weight: 700;
  text-decoration: none;
  box-sizing: border-box;
  background: #1457ff;
  transition: all 0.3s ease;
}
.btn:hover {
  background: #154cd5;
}
.btn:focus {
  background: #002b95;
}

.btn-secondary {
  background: #00cc85;
}
.btn-secondary:hover {
  background: #00b979;
}
.btn-secondary:focus {
  background: #00a46b;
}

.btn-bordered {
  background: none !important;
  color: #FFFFFF;
  border: 2px solid currentColor;
  padding: 13px 14px;
}
.btn-bordered:hover {
  color: #efefef;
}
.btn-bordered:focus {
  color: #d0d0d0;
}

.btn-bordered-primary {
  background: none !important;
  color: #1457ff;
  border: 2px solid currentColor;
  padding: 13px 14px;
}
.btn-bordered-primary:hover {
  color: #154cd5;
}
.btn-bordered-primary:focus {
  color: #002b95;
}

.link-btn {
  font-size: 16px;
  font-weight: 700;
  display: inline-block;
  margin: 0 19px 0 0;
  color: #1457FF;
  position: relative;
  text-decoration: none;
  transition: all 0.3s ease;
}
.link-btn:hover {
  text-decoration: none;
  color: #154cd5;
}
.link-btn:hover::before {
  transform: scale(1);
}
.link-btn:focus {
  color: #002b95;
}
.link-btn:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: #154cd5;
  transform: scale(0);
  transition: all 0.2s ease;
}
.link-btn .arrow {
  width: 12px;
  height: 12px;
  display: block;
  position: absolute;
  top: 50%;
  right: -19px;
  transform: translateY(-50%);
}
.link-btn .arrow:before {
  content: "";
  width: 10px;
  height: 1px;
  background-color: currentColor;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
.link-btn .arrow:after {
  content: "";
  width: 8px;
  height: 8px;
  border-right: 1px solid currentColor;
  border-bottom: 1px solid currentColor;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%) rotate(-45deg);
}

/* wrapper */
#wrapper {
  overflow: hidden;
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  transition: all 0.2s ease;
  position: relative;
  padding: 60px 0 0;
}

.main {
  flex: 1 1 auto;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 20px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.color {
  color: #1457FF;
}

.green-color {
  color: #00CC85;
}

.scroll-down {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: block;
  position: relative;
  background-color: #00cc85;
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  z-index: 3;
  transition: background-color 0.3s ease;
}
.scroll-down:before {
  content: "";
  width: 2px;
  height: 14px;
  background-color: #FFFFFF;
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
}
.scroll-down:after {
  content: "";
  width: 12px;
  height: 12px;
  border-left: 2px solid #FFFFFF;
  border-bottom: 2px solid #FFFFFF;
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%) rotate(-45deg);
}
.scroll-down:hover {
  background-color: #00b979;
}
.scroll-down:focus {
  background-color: #00a46b;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 20px;
  z-index: 25;
}
.modal .close {
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(97, 100, 114, 0.8);
  width: 100%;
  height: 100%;
}

.modal-hold {
  max-width: 944px;
  width: 100%;
  margin: 0 auto;
  background-color: #FFFFFF;
  border-radius: 15px;
  box-shadow: 0px 4px 20px 0px rgba(110, 110, 110, 0.25);
  padding: 0;
  position: relative;
  z-index: 26;
}

.modal-header {
  padding: 36px 45px 11px 20px;
  position: relative;
}
.modal-header h4 {
  font-size: 33px;
  margin: 0;
}

.close-modal {
  width: 18px;
  height: 18px;
  position: absolute;
  top: 4px;
  right: 10px;
  cursor: pointer;
}
.close-modal:before, .close-modal:after {
  content: "";
  width: 8px;
  height: 1px;
  background-color: #92949E;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.close-modal:after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.modal-body {
  padding: 0 20px 20px;
}
.modal-body__text {
  font-size: 14px;
  line-height: 1.43;
  max-height: 70vh;
  overflow: hidden;
  overflow-y: auto;
  position: relative;
}

@media (min-width: 768px) {
  #wrapper {
    padding: 111px 0 0;
  }
  h1 {
    font-size: 40px;
    line-height: 1.15;
  }
  h2, h3 {
    font-size: 40px;
    line-height: 1.15;
  }
  h4, h5, h6 {
    font-size: 28px;
    line-height: 1.14;
  }
  .scroll-down {
    width: 48px;
    height: 48px;
    bottom: 18px;
  }
  .scroll-down:before {
    width: 3px;
    height: 18px;
    bottom: 17px;
  }
  .scroll-down:after {
    width: 14px;
    height: 14px;
    border-width: 3px;
    bottom: 17px;
  }
  .modal-hold {
    border-radius: 26px;
  }
  .modal-header {
    padding: 63px 40px 18px 40px;
  }
  .modal-body {
    padding: 15px 40px 63px;
  }
  .close-modal {
    top: 39px;
    right: 54px;
  }
  .close-modal:before, .close-modal:after {
    content: "";
    width: 20px;
    height: 3px;
    background-color: #92949E;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .close-modal:after {
    transform: translate(-50%, -50%) rotate(45deg);
  }
}
@media (min-width: 1024px) {
  .modal-header {
    padding: 63px 80px 18px 80px;
  }
  .modal-body {
    padding: 15px 80px 63px;
  }
  .close-modal {
    right: 94px;
  }
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

#header {
  display: flex;
  padding: 14px 0;
  background-color: #F9FAFF;
  box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.035);
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
}
#header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#header.sticky {
  position: fixed;
  top: 0;
  left: 0;
}

.logo {
  display: block;
  flex: 0 0 96px;
  width: 96px;
  height: 29px;
  margin: 0 10px 0 0;
}
.logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}

.header-btn__mobile {
  display: inline-flex;
  align-items: center;
  min-height: 32px;
}
.header-btn__mobile .btn {
  font-size: 12px;
  line-height: 1.17;
  padding: 9px 8px;
  border-radius: 8px;
}
.open-nav .header-btn__mobile .btn {
  display: none;
}

.toogle-menu {
  display: block;
  z-index: 101;
  width: 27px;
  height: 18px;
  margin: 0 5px 0 12px;
  position: relative;
  text-indent: -99999px;
  cursor: pointer;
}
.toogle-menu > span {
  position: absolute;
  left: 0;
  height: 3px;
  width: 100%;
  background: #080A13;
}
.toogle-menu .t {
  top: 0;
  transform: rotate(0deg);
  transition: transform 0.25s cubic-bezier(0.4, 0.01, 0.165, 0.99) 0s, top 0.25s cubic-bezier(0.4, 0.01, 0.165, 0.99) 0.3s;
}
.toogle-menu .c {
  top: 7px;
  transition: opacity 0.2s ease-in-out;
}
.toogle-menu .b {
  top: 15px;
  transform: rotate(0deg);
  transition: transform 0.25s cubic-bezier(0.4, 0.01, 0.165, 0.99) 0s, top 0.25s cubic-bezier(0.4, 0.01, 0.165, 0.99) 0.3s, width 0.2s ease;
}

.open-nav {
  overflow: hidden;
}
.open-nav .toogle-menu > span {
  background: #92949E;
}
.open-nav .toogle-menu .t {
  top: 6px;
  transform: rotate(45deg);
  transition: transform 0.25s cubic-bezier(0.4, 0.01, 0.165, 0.99) 0.3s, top 0.25s cubic-bezier(0.4, 0.01, 0.165, 0.99) 0s;
}
.open-nav .toogle-menu .c {
  opacity: 0;
}
.open-nav .toogle-menu .b {
  top: 6px;
  width: 100%;
  transform: rotate(135deg);
  transition: transform 0.25s cubic-bezier(0.4, 0.01, 0.165, 0.99) 0.3s, top 0.25s cubic-bezier(0.4, 0.01, 0.165, 0.99) 0s;
}

.nav-holder {
  position: fixed;
  top: 60px;
  left: 0;
  width: 100%;
  height: calc(100% - 60px);
  padding: 30px 0;
  background: #F9FAFF;
  z-index: 100;
  overflow: hidden;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  opacity: 0;
  visibility: hidden;
  min-width: 320px;
  transition: all 0.2s ease-in-out;
}
.nav-holder #nav {
  display: flex;
  height: 100%;
  padding: 0 20px;
  margin: 0 0 48px;
  flex-direction: column;
  justify-content: center;
}
.open-nav .nav-holder {
  opacity: 1;
  visibility: visible;
}
.nav-holder .btn {
  max-width: 245px;
  margin: 0 auto;
  width: 100%;
}

.menu {
  display: flex;
  flex-direction: column;
  margin: 0 0 20px;
}
.menu li {
  text-align: center;
  padding: 22px 0;
}
.menu li a {
  font-size: 23px;
  line-height: 1.17;
  font-size: 500;
  color: #080A13;
  text-decoration: none;
  display: inline-block;
  transition: all 0.3s ease;
}
.menu li a:hover, .menu li a.active {
  text-decoration: underline;
  color: #1457FF;
}

.item-lang {
  display: none;
}

.list-lang {
  margin-top: auto;
  display: flex;
  justify-content: center;
  padding: 0 20px;
}
.list-lang li {
  margin: 0 10px;
}
.list-lang li a {
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  color: #080A13;
  display: inline-block;
  padding: 6px;
  text-decoration: none;
}

@media (min-width: 768px) {
  #header {
    padding: 30px 20px;
  }
  #header .logo {
    flex: 0 0 167px;
    width: 167px;
