  .menu {
    flex-direction: row;
    margin: 0 10px 0 0;
  }
  .menu li {
    padding: 0 10px;
  }
  .menu li a {
    font-size: 16px;
    font-weight: 700;
  }
  .hold-lang {
    position: relative;
    width: 48px;
    margin: 0 0 0 20px;
  }
  .hold-lang .item-lang {
    font-size: 14px;
    font-weight: 700;
    color: #616472;
    text-transform: uppercase;
    display: block;
    padding: 4px 26px 4px 4px;
