.section-product {
  background-color: #F3F3F3;
  padding: 30px 0;
}
.section-product h3 {
  text-align: center;
  margin: 0 0 30px;
}
.section-product__list {
  display: flex;
  flex-direction: column;
  padding: 0 14px;
}
.section-product__item {
  width: 100%;
  background-color: #F9FAFF;
  border-radius: 20px;
  padding: 16px;
  margin: 0 0 16px;
  min-height: 363px;
}
.section-product__media {
  width: 100%;
  margin: 0 0 27px;
  box-shadow: 0px 100px 80px 0px rgba(0, 0, 0, 0.07);
}
.section-product__media a {
  display: block;
  width: 100%;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
}
.section-product__media .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background: rgba(0, 0, 0, 0.6);
}
.section-product__media .overlay:before {
  content: "";
  width: 50px;
  height: 50px;
  border: 5px solid #FFFFFF;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}
.section-product__media .overlay:after {
  content: "";
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 11px 0 11px 18px;
  border-color: transparent transparent transparent #FFFFFF;
  transform: rotate(0deg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 0 0 3px;
  z-index: 3;
}
.section-product__media img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
  position: relative;
  z-index: 1;
}
.section-product__info {
  position: relative;
  font-size: 14px;
  line-height: 1.4;
}
.section-product__title {
  font-size: 19px;
  font-weight: 500;
  display: block;
  margin: 0 0 11px;
}

@media (min-width: 768px) {
  .section-product {
    padding: 125px 0;
  }
  .section-product h3 {
    margin: 0 0 70px;
  }
  .section-product__list {
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 -8px 9px;
    width: auto;
  }
  .section-product__item {
    width: calc(50% - 16px);
    margin: 0 8px 16px;
  }
  .section-product__title {
    font-size: 23px;
    margin: 0 0 10px;
  }
}
@media (min-width: 1200px) {
  .section-product__item {
    display: flex;
    flex-direction: row-reverse;
    min-height: inherit;
  }
  .section-product__media {
    width: calc(50% - 10px);
    margin: 0 0 0 10px;
    box-shadow: none;
  }
  .section-product__info {
    width: 50%;
    padding: 0 10px 0 0;
  }
}
/*end section-product*/
.fancybox__backdrop {
  background-color: rgba(97, 100, 114, 0.8);
}

.fancybox__carousel {
  padding: 0 20px;
}

.fancybox__slide {
  padding: 0;
  margin: 0;
}

.fancybox__slide.has-image > .fancybox__content {
  width: 100% !important;
  height: auto !important;
  background-color: #FFFFFF;
  border-radius: 15px;
  box-shadow: 0px 4px 20px 0px rgba(110, 110, 110, 0.25);
  padding: 22px 10px 10px 10px;
}

.fancybox__content .close-button {
  width: 18px;
  height: 18px;
  position: absolute;
  top: 4px;
  right: 10px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}
.fancybox__content .close-button:before, .fancybox__content .close-button:after {
  content: "";
  width: 8px;
  height: 1px;
  background-color: #92949E;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.fancybox__content .close-button:after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.fancybox__toolbar__column {
  display: none !important;
}

.fancybox-image {
  border-radius: 10px;
}

@media (min-width: 1024px) {
  .fancybox__slide.has-image > .fancybox__content {
    max-width: 944px;
    padding: 88px 82px 50px;
    border-radius: 26px;
  }
  .fancybox-image {
    border-radius: 26px;
  }
  .fancybox__content .close-button {
    top: 39px;
    right: 94px;
  }
  .fancybox__content .close-button:before, .fancybox__content .close-button:after {
    content: "";
    width: 20px;
    height: 3px;
    background-color: #92949E;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .fancybox__content .close-button:after {
    transform: translate(-50%, -50%) rotate(45deg);
  }
}
