/*start section-entry*/
.section-entry {
  padding: 60px 0 44px;
  position: relative;
}
.section-entry__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 272px;
  z-index: 1;
}
.section-entry__bg:before {
  content: "";
  width: 223px;
  height: 195px;
  background: url("../images/bg-green.svg") no-repeat 0 0;
  background-size: contain;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}
.section-entry__bg:after {
  content: "";
  width: 278px;
  height: 231px;
  background: url("../images/bg-blue.svg") no-repeat 0 0;
  background-size: contain;
  position: absolute;
  bottom: -94px;
  right: -48px;
  z-index: 2;
}
.section-entry__bg .element-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 272px;
  background-color: #E7EFFF;
  filter: blur(43px);
  z-index: 1;
}
.section-entry .container {
  max-width: 990px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 3;
}
.section-entry__image {
  max-width: 302px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 26px;
}
.section-entry__image img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.section-entry__desc {
  position: relative;
  max-width: 770px;
  margin: 0 auto;
}
.section-entry__suptitle {
  font-size: 11px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  color: #81838e;
  display: block;
  margin: 0 0 10px;
}

@media (min-width: 768px) {
  .section-entry {
    padding: 90px 0;
